@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@400;500;700&display=swap);

#identity-information {
    overflow: hidden;
    position: relative;
    width: 100%;
    height: 70px;
    margin-top: 20px;
    margin-bottom: 100px;
}

#name {
    display: inline-block;
    font-size: 35px;
    font-family: 'Ubuntu';
    font-weight: 500;
    color: rgb(103, 179, 116);
    border: none;
    text-align: center;
    width: 60%;
}

#contact-info {
    float: left;
    display: flex;
    width: 20%;
    height: 100%;
    padding-left: 3px;
    flex-direction: column;
    font-size: 10px;
}

#contact-info input, #contact-info span {
    width: 100px;
    margin-top: 3px;
    border: none;
    font-family: 'Ubuntu';
    font-size: 10px;
}

#contact-info label {
    display: block;
    text-align: left;
    margin-top: 5px;
    font-family: 'Ubuntu';
    color: rgb(103, 179, 116);
}
.education {
    margin-left: 30px;
    margin-right: 30px;
    font-family: 'Ubuntu';
    font-size: 15px;
}

input {
    font-family: 'Ubuntu';
    font-size: 15px;
}

label {
    font-weight: 500;
    display: inline-block;
    text-align: right;
}

.education input {
    margin-right: 20px;
}

.date-container {
    position: absolute;
    top: 5px;
    right: 5px;
    display: flex;
    flex-direction: row;
}

.date-container h2 {
    margin-bottom: 50px;
}

.date-container label {
    font-size: 10px;
    margin-right: 5px;
}

.date-container input, .date-container span {
    width: 70px;
    height: 10px;
    font-size: 10px;
}

.educational-experience-item {
    position: relative;
    max-height: 120px;
    margin-top: 30px;
    padding: 10px;
    border: 1pt solid gray;
}

.school-and-study {
    display: flex;
    margin-top: 50px;
    margin-bottom: 20px;
}

.school-and-study label {
    margin-right: 10px;
}

.school, .study {
    margin-right: 20px;
}

#add-educational-item-button {
    margin-top: 10px;
}

button {
    cursor: pointer;
    width: 45px;
    height: 23px;
    font-size: 10px;
    border: 1pt solid black;
    color: white;
    font-weight: bold;
    background-color: grey;
}

button:hover {
    box-shadow: 0px 0px 10px grey;
}

.invalid-date {
    border: 1pt solid red;
}

.end-date-container, .start-date-container {
    width: 100px;
}

.date-alert {
    color: red;
}
.work {
    margin-left: 30px;
    margin-right: 30px;
    font-family: 'Ubuntu';
    font-size: 15px;
}

input {
    font-family: 'Ubuntu';
    font-size: 15px;
}

label {
    font-weight: 500;
    display: inline-block;
    text-align: right;
}

.work input {
    margin-right: 20px;
}

.date-container {
    position: absolute;
    top: 5px;
    right: 5px;
    display: flex;
    flex-direction: row;
}

.date-container h2 {
    margin-bottom: 50px;
}

.date-container label {
    font-size: 10px;
    margin-right: 5px;
}

.date-container input, .date-container span {
    width: 70px;
    height: 10px;
    font-size: 10px;
}

.practical-experience-item {
    position: relative;
    max-height: 200px;
    margin-top: 30px;
    padding: 10px;
    border: 1pt solid gray;
    
}

.non-date {
    display: flex;
    margin-top: 50px;
    margin-bottom: 20px;
}

.non-date label {
    margin-right: 10px;
}

.company, .position, .tasks {
    margin-right: 20px;
}


#add-practical-item-button {
    margin-top: 10px;
}

.invalid-date {
    border: 1pt solid red;
}

.end-date-container, .start-date-container {
    width: 100px;
}

.date-alert {
    color: red;
}

html {
    height: 100%;
    width: 100%;
}

body {
    margin-left: 15%;
    margin-right: 15%;
    padding-left: 10px;
    padding-right: 10px;
    margin-top: 0px;
    min-height: 100%;
    border-right: 1pt solid rgb(182, 182, 182);
    border-left: 1pt solid rgb(182, 182, 182);
    border-top: 1pt solid rgb(182, 182, 182);
}

.identity {
    margin-bottom: 50px;
}

.education {
    margin-bottom: 50px;
}

#edit-CV-button, #submit-CV-button {
    cursor: pointer;
    margin-top: 40px;
    margin-bottom: 30px;
    margin-left: 30px;
    width: 60px;
    height: 30px;
    font-size: 13px;
    border: 1pt solid black;
    color: white;
    font-weight: bold;
    background-color: rgb(103, 179, 116);
}

#edit-CV-button:hover, #submit-CV-button:hover {
    box-shadow: 0px 0px 10px grey;
}
